// src/app/admin/article/page.tsx
"use client";
import React, { useEffect, useState } from "react";
import Link from "next/link";
import Cookies from "js-cookie";
import { Plus, Trash2, Loader2, Edit, ExternalLink } from "lucide-react";
import { Article } from "@/types";

export default function AdminArticlePage() {
  const [articles, setArticles] = useState<Article[]>([]);
  const [loading, setLoading] = useState(true);

  // 1. Ambil URL Backend dari Environment
  const baseUrl = process.env.NEXT_PUBLIC_BASE_URL || "http://localhost:3000";

  const fetchArticles = async () => {
    // 2. Ambil Token untuk otentikasi
    const token = Cookies.get("token");
    
    try {
      // 3. Gunakan baseUrl dinamis & Kirim Token
      const res = await fetch(`${baseUrl}/api/article`, { 
        cache: "no-store",
        headers: {
            "Content-Type": "application/json",
            "Authorization": `Bearer ${token}` // <--- PENTING: Kirim token
        }
      });
      
      if (!res.ok) throw new Error("Gagal ambil data");
      setArticles(await res.json());

    } catch (error) { 
        console.error(error); 
    } finally { 
        setLoading(false); 
    }
  };

  const handleDelete = async (id: string) => {
    if (!confirm("Yakin ingin menghapus artikel ini?")) return;
    const token = Cookies.get("token");
    
    try {
        const res = await fetch(`${baseUrl}/api/article/${id}`, {
            method: "DELETE",
            headers: { "Authorization": `Bearer ${token}` }
        });
        if (res.ok) {
            alert("Artikel berhasil dihapus");
            fetchArticles();
        }
    } catch (error) {
        alert("Gagal menghapus artikel");
    }
  };

  useEffect(() => { fetchArticles(); }, []);

  return (
    <div>
      <div className="flex items-center justify-between mb-8">
        <div>
          <h1 className="text-3xl font-bold font-heading">Kelola Artikel</h1>
          <p className="text-gray-400 text-sm">Daftar berita dan artikel himpunan.</p>
        </div>
        <Link href="/admin/article/create" className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-lg flex items-center gap-2 font-bold text-sm transition-colors">
          <Plus size={18} /> TAMBAH ARTIKEL
        </Link>
      </div>

      <div className="bg-zinc-900 border border-white/10 rounded-xl overflow-hidden shadow-xl">
        {loading ? (
            <div className="p-12 text-center flex flex-col items-center gap-3 text-gray-500">
                <Loader2 className="animate-spin w-8 h-8 text-blue-500" />
                <span className="text-sm">Memuat artikel...</span>
            </div>
        ) : articles.length === 0 ? (
            <div className="p-12 text-center text-gray-500">
                Belum ada artikel. Klik tombol "Tambah Artikel" untuk membuat baru.
            </div>
        ) : (
          <table className="w-full text-left border-collapse">
            <thead className="bg-white/5 text-gray-400 text-xs uppercase tracking-wider">
              <tr>
                <th className="p-4 border-b border-white/10">Judul Artikel</th>
                <th className="p-4 border-b border-white/10">Tanggal Upload</th>
                <th className="p-4 border-b border-white/10">Status</th>
                <th className="p-4 border-b border-white/10 text-right">Aksi</th>
              </tr>
            </thead>
            <tbody className="divide-y divide-white/10">
              {articles.map((item) => (
                <tr key={item.id} className="hover:bg-white/5 transition-colors">
                  <td className="p-4 font-medium text-white max-w-md truncate">
                    {item.title}
                  </td>
                  <td className="p-4 text-gray-400 text-sm">
                    {/* Gunakan suppressHydrationWarning agar tanggal tidak error */}
                    <span suppressHydrationWarning>
                        {new Date(item.createdAt).toLocaleDateString("id-ID", {
                            day: 'numeric', month: 'long', year: 'numeric'
                        })}
                    </span>
                  </td>
                  <td className="p-4">
                     {/* Contoh Badge Status Sederhana */}
                     <span className="px-2 py-1 rounded text-[10px] font-bold border bg-green-500/10 text-green-400 border-green-500/20 uppercase">
                        PUBLISHED
                     </span>
                  </td>
                  <td className="p-4 text-right flex justify-end gap-2">
                    {/* Tombol Lihat (Opsional) */}
                    <Link href={`/article/${item.id}`} target="_blank" className="p-2 bg-blue-900/30 text-blue-400 rounded-lg hover:bg-blue-600 hover:text-white transition-all">
                        <ExternalLink size={16} />
                    </Link>
                    
                    {/* Tombol Hapus */}
                    <button onClick={() => handleDelete(item.id)} className="p-2 bg-zinc-800 text-red-400 border border-white/10 rounded-lg hover:bg-red-900/50 hover:text-red-400 hover:border-red-500/30 transition-all">
                        <Trash2 size={16} />
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        )}
      </div>
    </div>
  );
}